import { useState } from 'react';
import './App.css';
import classes from './app.module.css';


function App() {

  const[count,setCount] = useState(0);

  return (
    <div className="App">
      <h1>{count}</h1>
      <div>
      <button className={classes.container} onClick={() => setCount(count-1)}>-</button>
      <button className={classes.container} onClick={() => setCount(count+1)}>+</button>
      </div>
    </div>
  );
}

export default App;
